html, .body {
  font-family: "TT Lakes";
  background-color: #f8f8f8;
  overflow-x: hidden;
  overflow-y: auto;
  height: 100%;
  position: relative;
  bottom: 0; }

.icons-and-header__icon_burger {
  width: 40px;
  height: 40px;
  background-image: url(./static/burger.svg);
  background-repeat: no-repeat; }

.icons-and-header__icon_logo {
  width: 100px;
  height: 60px;
  background-image: url(./static/Group.svg);
  background-repeat: no-repeat; }

.icons-and-header__icon_divider {
  width: 3px;
  height: 40px;
  background-image: url(./static/divider.svg); }

.icons-and-header__icon_repair {
  width: 40px;
  height: 40px;
  background-image: url(./static/repair.svg);
  background-repeat: no-repeat; }

.icons-and-header__icon_status {
  width: 40px;
  height: 40px;
  background-image: url(./static/checkstatus.svg); }

.icons-and-header__icon_call {
  width: 40px;
  height: 40px;
  background-image: url(./static/call.svg);
  background-repeat: no-repeat; }

.icons-and-header__icon_profile {
  width: 40px;
  height: 40px;
  background-image: url(./static/profile.svg);
  background-repeat: no-repeat; }

.icons-and-header__icon_lg {
  display: none; }

.icons-and-header__icon_md {
  display: none; }

.icons-and-header__icon_divider {
  display: none; }

.icons-and-header__link {
  display: none; }

.icons-and-header__header {
  display: none; }

.icons-and-header__icon_sm {
  display: block;
  align-self: center; }

.icons-and-header {
  display: flex;
  background-color: #ffffff;
  padding: 24px 16px 18px 16px;
  justify-content: space-between; }

.head-md {
  font-size: 28px;
  margin-top: 24px;
  margin-left: 16px;
  position: relative; }

.head-md::before {
  content: '';
  width: 4px;
  height: 40px;
  background-image: url(./static/highlight.svg);
  position: absolute;
  top: -5px;
  left: -15px; }

.menu {
  margin-top: 20px;
  margin-left: 16px;
  overflow-x: auto; }

.menu__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style-type: none;
  flex-wrap: wrap;
  min-width: 1120px;
  overflow-x: auto;
  justify-content: space-between; }

.menu__item {
  border-radius: 6px;
  box-sizing: border-box;
  padding: 10px; }

.menu__item_blue {
  border: 2px solid #b8ffec;
  background-color: #ffffff; }

.menu__link {
  text-decoration: none;
  color: #1b1c21;
  font-size: 16px;
  display: block;
  transition: opacity 1s; }

.menu__link:hover {
  opacity: 0.4; }

.content {
  margin-top: 24px;
  display: flex;
  flex-direction: column; }

.content__text {
  margin: 0 16px;
  display: flex;
  flex-direction: column; }

.content__link-block {
  margin-left: 48px; }

.content__link {
  text-decoration: none;
  color: #1b1c21;
  font-size: 16px;
  position: relative;
  transition: opacity 1.5s; }

.content__link:hover {
  opacity: 0.4; }

.content__link::before {
  content: "";
  background-image: url(./static/expand.svg);
  background-repeat: no-repeat;
  width: 50px;
  height: 40px;
  position: absolute;
  left: -30px;
  top: -3px; }

.content__el_md {
  display: none; }

.content__image-block {
  margin-top: 20px; }

.content__img_el {
  width: 100%;
  margin: 0;
  border-radius: 6px; }

.swiper-container {
  margin-top: 16px; }

.swiper-pagination-bullet-active {
  background-color: #b5b6bc !important; }

.swiper-slide {
  background-color: #ffffff;
  margin-left: 16px;
  border-radius: 6px;
  border: 1px solid #EAEAEA;
  justify-content: space-between;
  align-items: center;
  display: flex;
  padding: 10px; }

.go {
  background-image: url(./static/go.svg);
  background-repeat: no-repeat;
  position: relative;
  top: 5px;
  width: 50px;
  height: 50px; }

.logo-of-companies__link_down {
  display: none; }

.logo-of-companies__el_click {
  display: none; }

.logo-of-companies__link_up {
  display: none; }

@media (min-width: 768px) {
  .swiper-slide {
    margin-left: 0;
    width: auto;
    height: auto; }
  .swiper-container {
    height: 100%; } }

.head {
  background: linear-gradient(270deg, #f8f8f8 0%, #ffffff 98.61%);
  padding: 16px;
  border-top: 2px solid #D9FFF5;
  margin-bottom: 16px;
  padding-left: 4%;
  margin-top: 20px; }

.head__el {
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  color: #7e7e82;
  text-transform: uppercase; }

@media (min-width: 768px) {
  .head {
    background-image: linear-gradient(90deg, #f8f8f8 1.39%, #ffffff 100%); } }

@media (min-width: 1120px) {
  .head {
    background-image: linear-gradient(270deg, #f8f8f8 0%, #ffffff 98.61%); } }

.services__el {
  padding: 16px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  border-radius: 6px;
  border: 1px solid #EAEAEA;
  background-color: #ffffff; }

.next {
  background-image: url(./static/go.svg);
  background-repeat: no-repeat;
  position: relative;
  top: 5px;
  width: 50px;
  height: 50px;
  align-self: flex-end; }

.services__text {
  margin-bottom: 120px;
  align-self: flex-start; }

.swiper-pagination_second {
  bottom: -500px !important; }

.prices__block {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative; }

.prices__btn {
  position: absolute;
  bottom: 16px;
  left: 130px;
  background-color: #ff3e79;
  border-radius: 16px; }

.button {
  display: flex;
  justify-content: space-around; }

.button__text {
  text-transform: uppercase;
  font-size: 12px;
  color: #ffffff;
  line-height: 24px;
  margin: 4px 10px 4px 12px; }

.button__icon {
  background-image: url(./static/goside.svg);
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  align-self: center;
  position: relative;
  top: -3px; }

.prices__header {
  color: #7E7E82;
  font-weight: normal; }

.warn {
  display: flex;
  margin: 16px 16px 0px 16px;
  padding-right: 20px;
  color: #1b1c21;
  flex-direction: column; }

.warn__link {
  text-decoration: none;
  font-size: 16px;
  color: #1b1c21;
  position: relative; }

.warn__link::after {
  content: '';
  background-image: url(./static/more.svg);
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  position: absolute;
  top: -4px; }

@media (min-width: 240px) and (max-width: 345px) {
  .warn__link::after {
    top: 15px; } }

.hr-blue {
  color: #D9FFF5;
  margin-top: 40px;
  margin-bottom: 16px; }

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 3px; }

.footer__text {
  opacity: 0.72; }

.footer__text_team {
  margin-top: -15px; }

.footer__text_page {
  margin-top: -15px;
  margin-bottom: 16px; }

.footer__text_policy {
  margin-top: 0;
  margin-bottom: 16px; }

.footer__text_info {
  margin-top: 0; }
